@import "~assets/common.scss";

.header {
    @extend %viewport-height;
    position: relative;
    padding: 0 !important;

    .container {
        @extend %center-all;
        text-align: center;
        width: 100%;
    }

    &__subtitle {
        font-weight: 500;
        border-radius: $border-radius-base;
    }
}

.subtitle {
    &__first-word {
        font-weight: 800;

        &__first-letter {
            @extend %secondary-color;
        }
    }
}



